html {
    --scrollbarBG: black;
    --thumbBG: #7F81BA;
    --gradBG: #26284C;
}
body::-webkit-scrollbar {
    width: 12px;
}
body {
    scrollbar-color: #7F81BA black;
    scrollbar-color: var(--thumbBG) var(--scrollbarBG);
}
body::-webkit-scrollbar-track {
    background: black;
    background: var(--scrollbarBG);
}
body::-webkit-scrollbar-thumb {
    /* background-image: linear-gradient(var(--thumbBG), var(--gradBG)); */
    background-color: #7F81BA;
    background-color: var(--thumbBG) ;
    border-radius: 999px;
}

* {
    margin: 0;
    font-family: roboto;
}

.search {
    display: flex;
    width:285px;
    height: 40px;
    background: rgba(196, 196, 196, 0.33);
    border-radius: 62px;
    margin: auto;
}

.search_logo {
    padding: 1rem;
    padding-right: 0;
    height: 40px;
    display: none;
}

.search_logo_big {
    height: 60px;
    padding: 1rem;
    display: block;
}

.searchicon {
    margin: auto;
    margin-right: 6%;
}
.searchBody{
    padding: 4em 18em 2em 5em;
    background-color:#F7F7FB;
}
.searchRes{
    margin: auto !important;
    width:15em;
    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    letter-spacing: 0.145em;
    text-transform: uppercase;
    color: #26284C;
    white-space: nowrap;
    margin-right: 1rem;
}

.hrLine{
    margin: auto;
    width: 90%;
    background-color: black;
    opacity: 0.7;
    height:1.3px;
}
.searchContainer {
    margin-top: 2em;
    border-radius: 8px;
    background-color: white;
    box-shadow: 0px 4px 8px rgba(127, 57, 251, 0.04);
}

.searchCardBtn{
    position: absolute;
    right:18em;
    padding-top: 1em;
}
.searchCardBtn >button{
    padding: 4px 24px 4px 20px;
    background: rgba(3, 218, 197, 0.9);
    border-radius: 100px 0px 0px 100px;
    text-transform: capitalize !important;
    font-size: 13px !important;
}
.searchGrid {
    padding: 2em 2.5em !important;
}
.searchContainer:hover {
    box-shadow: 0px 8px 24px rgba(30, 31, 59, 0.08) !important;
}
.searchGrid> h4 {
    color:black;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
}
.searchGrid > h4:hover{
    color: black;
    opacity: 0.8 !important;
}
.searchGrid > h6 {
    color: #1E1F3B;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    margin-top: 1em;
}
.searchCardImage>img{
    height:100%;
    width:100%;
    object-fit:cover;
    border-radius:8px;
}
.searchRightMobile {
    display: none;
}

.nav-inputbase {
    width: 100%;
    margin-top:4px !important;
    margin-left:1em;
}

.filter_categories {
    display: flex;
    align-items: center;
    overflow-x: scroll;
    scroll-behavior: smooth;   
}

.filter_categories::-webkit-scrollbar {
    display: none;
}
  
.filter_categories {
    -ms-overflow-style: none; 
    scrollbar-width: none;  
}


.filter_categories > h5 {
    height: 23px;   
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 161.19%;    
    letter-spacing: 0.08em;
    text-transform: uppercase;
    margin: 2rem;
    color: rgba(0, 0, 0, 0.87);
    white-space: nowrap;
}


.filter_categories > button {
    padding:1em 2em;
    height: 35px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    border-radius: 54px;
    margin: 1rem;
    margin-right: 1rem;
    outline: none;
    border-color: #5A5EBB;
}

@media (min-width:680px) and (max-width: 960px) {
    .searchicon{
        margin-right: 10%;
    }
    .search {
        display: flex;
        width:235px;
        height: 40px;
        margin: auto;
    }
    .nav-inputbase {
        width: 90%;
    }
    .filter_categories > h5 {
        font-size: 12px;
        margin: 1rem;
    }
    .filter_categories > h5 > .d-none {
        display: none;
    }


    .filter_categories>button {
        padding: 1em 2em;
        height: 35px;
        font-size: 10px;
        margin: 20px 10px;
    }
    .searchBody {
        padding: 4em 9em 2em 3em;
    }
    .searchRes{
        width:17em;
        font-size: 17px;
    }
    
    .searchContainer {
        margin-top: 1.5em;
    }

    .searchCardBtn{
        position: absolute;
        right:9em;
        padding-top: 1em;
    }
    .searchGrid {
        padding: 1.5em 2em !important;
    }
    .searchGrid> h4 {
        font-weight: 500;
        font-size: 22px;
    }
    .searchGrid > h6 {
        font-size: 13px;
        line-height: 18px;
    }
    .search_logo {
        display: block;
    }
    .search_logo_big {
        display: none;
    }

}

@media screen and (max-width: 680px) {

    .searchbar-mobile{
        display: flex;
        font-size: 8px !important;
    }
    .search {
        display: flex;
        width:130px;
        height:32px;
    }
    .nav-inputbase {
        width: 80%;
        margin-top:2px !important;
        margin-left:1em;
        font-size: 14px !important;
    }
    .filter_categories>h5 {
        font-size: 11px;
        margin: 1rem;
        margin-right: 0 !important;
        width:27em;
    }
    .filter_categories>button {
        padding: 1em 1.7em;
        height: 35px;
        font-size: 8px;
        font-weight: 500;
        margin-right: 0px !important;
        margin-bottom: 15px;
    }

    .searchBody {
        padding: 3em 1em;
    }

    .searchRes {
        width: 17em;
        font-size: 15px;
    }

    .searchContainer {
        margin-top: 1em;
    }

    .searchCardBtn {
        position: absolute;
        left: 1em;
        padding-top: 1em;
    }
    .searchCardBtn>button {
        border-radius: 0 21px 21px 0;
        font-size: 11px !important;
    }
    .searchGrid {
        padding: 1em 1em !important;
    }

    .searchGrid>a>h4 {
        font-weight: 400;
        font-size: 18px;
        margin-top: 2.5em;
    }

    .searchGrid>h6 {
        display: none !important;
    }
    .search_logo {
        display: block;
    }
    .search_logo_big {
        display: none;
    }
    .filter_categories > h5 > .d-none {
        display: none;
    }

}





